import { CF } from '../../utils/cfApi'
const cf = new CF()
export default {
	data(){
		return {
			title: '', //文章标题
			html: '', // 接收展示的html,
			IsCollection: 0, // 文章是否被收藏
			articleId: '', // 百科编号
			WomanId: '' // 用户编号
		}
	},
	onLoad(options) {
		this.GetArticleById (options)
	},
	// 
	methods: {	
		// 获取详情
		GetArticleById(options) {
			console.log(options)
			this.articleId = options.articleId
			this.WomanId = options.WomanId
			let data = {
				articleId: this.articleId ,
				WomanId: this.WomanId
			}
			cf.GetArticleById(data).then((res)=>{
				const dtData = res.dtData[0]
				console.log(res)
				this.title= dtData.ArticleTitle
				this.IsCollection = dtData.IsCollection
				this.html = this.htmlEscape(dtData.ArticleContent) 
			})
		},
		
		// 点击收藏或者 取消收藏
		collect () {
			this.IsCollection == 0 ? this.SaveMyCollection() : this.DeleteMyCollection()	
		},
		
		// 收藏请求
		SaveMyCollection(){
			const data = {
				id: this.articleId,
				WomanId: this.WomanId
			}
			
			cf.SaveMyCollection(data).then((res)=>{
				console.log(res)
				this.IsCollection = 1
			
				
				uni.showToast({
				    title: '收藏成功',
				    duration: 2000
				});
			})
		},
		
		// 取消收藏
		DeleteMyCollection() {
			const data = {
				id: this.articleId,
				WomanId: this.WomanId
			}
			cf.DeleteMyCollection(data).then((res)=>{
				console.log(res)
				this.IsCollection = 0
				uni.showToast({
				    title: '取消收藏成功',
				    duration: 2000
				});
			})
		},
		
		// 
		
		
		// 整理文档里的特殊符号
		htmlEscape (html){
		    var reg = /(&lt;)|(&gt;)|(&amp;)|(&quot;)/g;
		    return html.replace(reg,function(match){
				switch(match){
					case "&lt;":
					    return "<";
					case "&gt;":
					    return ">"
					case "&amp;":
						return "&";
					case "&quot;":
						return "\""
				}
			});
		}	
	},
	onShareAppMessage(res) {
		return {
			title: this.title
		}
	}
}

